export default {
  components: {},
  data() {
    return {
      couponCentent: "",
      isShowCouponDialog: false
    };
  },
  //方法集合
  methods: {
    // 复制成功
    onCopySuccess(res) {
      this.isShowCouponDialog = false;
      this.$vux.toast.text("复制成功", "middle");
    },
    // 复制失败
    onCopyError(res) {
      this.$vux.toast.text("复制失败", "middle");
    },
    // 打开对话框
    openDialog(couponCentent) {
      // 获取口令内容
      this.couponCentent = couponCentent;
      this.isShowCouponDialog = true;
    },
    // 关闭对话框
    closeDialog() {
      this.isShowCouponDialog = false;
    }
  },
  mounted() {}
};



